<template lang="pug">
	div.card-box
		.card
			CardHeader(:status="assetStatus", title="Assets: Performance & Control", v-model="assetStatus")

			.assets-table
				.t-head
					.cl
						p Assets (4)

					.cl
						p Leads

					.cl
						p %

					.cl
						p Landing Page
						
					.cl
						p Titles

				.t-row(v-for="(item, index) in assetsList", v-bind:class="{'active': item.status == 'play'}")
					.cl
						.action(
							v-if="item.status",
							v-bind:class="{play: item.status == 'play'}"
							v-on:click="changeAssetStatus(index)"
						)

						p {{ item.name }}

					.cl
						p {{ item.leads }}

					.cl
						p {{ item.percents }}

					.cl
						.link(v-if="item.landing_page") Open
						.add(v-else) Add

					.cl
						DropList(:list="item.titlesList", :default="defaultTitle", type="tooltip")

			.chart-container
				.title
					p % of Leads by Asset Downloaded

				.chart
					.legends
						.single(v-for='item in assetsList', v-if="item.status")
							p {{ item.name }}

					.chart-draw
						.single-line(
							v-for='item in assetsList',
							v-if="item.status"
							:style='{width: item.leads / maxNumber * 100 + "%"}'
						)
							p {{ item.percents }}%

						.chart-info
							.single
								p 0

							.single
								p {{ maxNumber / maxDivide * 1 }}

							.single
								p {{ maxNumber / maxDivide * 2 }}

							.single
								p {{ maxNumber / maxDivide * 3 }}

							.single
								p {{ maxNumber / maxDivide * 4 }}

			button.btn.add-asset(type="button") ADD A NEW ASSET

</template>

<script>
	import CardHeader from "../components/title-checkbox.vue";
	import DropList from "../components/drop-list.vue";

	export default {
		name: 'Assets',
		components:{
			CardHeader,
			DropList
		},
		data() {
			return {
				assetStatus: false,
				defaultTitle: 'Titles captured with this asset',
				titlesList: {
					0: 'IT Manager',
					1: 'IT Director',
					2: 'CIO',
					3: 'IT Security Manager',
					4: 'CISO',
					5: 'IT VP',
					6: 'IT Security Director',
					7: 'IT Security VP'
				},
				assetsList:{
					0:{
						status: 'play',
						name: 'How to adopt a winning mindset in finance',
						leads: '166',
						percents: '23.1',
						landing_page: 'url',
						titlesList: {
							0: 'IT Manager',
							1: 'IT Director',
							2: 'CIO',
							3: 'IT Security Manager',
							4: 'CISO',
							5: 'IT VP',
							6: 'IT Security Director',
							7: 'IT Security VP'
						}
					},
					1:{
						status: 'paused',
						name: 'Finance Trends in 2020',
						leads: '128',
						percents: '17.8',
						landing_page: 'url',
						titlesList: {
							0: 'IT Manager',
							1: 'IT Director',
							2: 'CIO',
							3: 'IT Security Manager',
							4: 'CISO',
							5: 'IT VP',
							6: 'IT Security Director',
							7: 'IT Security VP'
						}
					},
					2:{
						status: false,
						name: 'Get on the path to financial digital transformation',
						leads: '-',
						percents: '-',
						landing_page: false,
						titlesList: {
							0: 'IT Manager',
							1: 'IT Director',
							2: 'CIO',
							3: 'IT Security Manager',
							4: 'CISO',
							5: 'IT VP',
							6: 'IT Security Director',
							7: 'IT Security VP'
						}
					},
					3:{
						status: false,
						name: 'Digital Finance Best Practices',
						leads: '-',
						percents: '-',
						landing_page: false,
						titlesList: {
							0: 'IT Manager',
							1: 'IT Director',
							2: 'CIO',
							3: 'IT Security Manager',
							4: 'CISO',
							5: 'IT VP',
							6: 'IT Security Director',
							7: 'IT Security VP'
						}
					}
				},
				maxNumber: 100,
				maxDivide: 4
			}
		},
		created(){
			this.setMaxInt();
		},
		methods: {
			setMaxInt(){
				var max = this.maxNumber;
				for (var item in this.assetsList) {
					var num = parseInt(this.assetsList[item].leads);
					if (max < num && !isNaN(num)) {
						max = num
					}
				}

				var r = parseInt(max.toString()[0]);
				check();
				var x;
				function check(){
					x = r * 100;
					if(x < max){
						r = r + 1;
						check();
					}
				}
				this.maxNumber = x;
			},
			changeAssetStatus(index){
				if(this.assetsList.[index].status == 'play'){
					this.assetsList.[index].status = 'pause'
				}else{
					this.assetsList.[index].status = 'play'
				}
			}
		}
	}
</script>